import axios, { AxiosRequestConfig } from 'axios';
import { Toast } from 'antd-mobile'

//URLaxiosurl
//process.env.NODE_ENV  baseURL 
let baseURL = process.env.NODE_ENV === 'development' ? '/api' : 'https://api.apiopen.top';

//
const timeout = 30000;

//axios
const service = axios.create({
    timeout,
    baseURL,
    //cookie true
    withCredentials: true
});

// headers  languagetoken
service.interceptors.request.use(
    (config: AxiosRequestConfig) => {
        //
        // let customHeaders: AxiosRequestHeaders = {
        //     language: 'zh-cn'
        // };
        // config.headers = customHeaders;
        return config
    },
    error => {
        console.log(error)
        Promise.reject(error)
    }
)

//axios
interface axiosTypes<T> {
    data: T;
    status: number;
    statusText: string;
}

//
//###codemsgresult
//###result 
interface responseTypes<T> {
    code: number,
    message: string,
    result: T
}

// promise then
const requestHandler = <T>(method: 'get' | 'post' | 'put' | 'delete', url: string, params: object = {}, config: AxiosRequestConfig = {}): Promise<T> => {
    let response: Promise<axiosTypes<responseTypes<T>>>;
    switch (method) {
        case 'get':
            response = service.get(url, { params: { ...params }, ...config });
            break;
        case 'post':
            response = service.post(url, { ...params }, { ...config });
            break;
        case 'put':
            response = service.put(url, { ...params }, { ...config });
            break;
        case 'delete':
            response = service.delete(url, { params: { ...params }, ...config });
            break;
    }

    return new Promise<T>((resolve, reject) => {
        response.then(res => {
            // 

            const data = res.data;
            if (data.code !== 200) {

                // 
                if (data.code === 401) {
                    Toast.show({
                        icon: 'fail',
                        content: '...',
                    })
                    console.log('...');
                }

                let e = JSON.stringify(data);
                Toast.show({
                    icon: 'fail',
                    content: `${e}`,
                })
                console.log(`${e}`)
                // reject
                reject(data);
            } else {
                // resolve
                resolve(data.result);
            }

        }).catch(error => {
            let e = JSON.stringify(error);
            Toast.show({
                icon: 'fail',
                content: `${e}`,
            })
            console.log(`${e}`)
            reject(error);
        })
    })
}

//  request getpostputdelete
const request = {
    get: <T>(url: string, params?: object, config?: AxiosRequestConfig) => requestHandler<T>('get', url, params, config),
    post: <T>(url: string, params?: object, config?: AxiosRequestConfig) => requestHandler<T>('post', url, params, config),
    put: <T>(url: string, params?: object, config?: AxiosRequestConfig) => requestHandler<T>('put', url, params, config),
    delete: <T>(url: string, params?: object, config?: AxiosRequestConfig) => requestHandler<T>('delete', url, params, config)
};

// 
export { request };